import{ useState, useEffect } from 'react';
const API_BASE = "https://shourabh-todo.herokuapp.com";  

function App() {
const [todos, setTodos] = useState([]);
const [popupActive, setPopupActive] = useState(false);
const [newTodo, setNewTodo] = useState("");


useEffect((todos)=>{
  GetTodos();
  console.log(todos);
}, [])


const GetTodos = () => {
  fetch(API_BASE + "/todos")
  .then(res => res.json())
  .then(data => setTodos(data))
  .catch(err=> console.error("Error: " ,err));
}

const completeTodo = async id => {
  const data = await fetch(API_BASE + "/todo/complete/" + id)
  .then(res => res.json());
  setTodos(todos => todos.map(todo => {
    if (todo._id === data._id){
      todo.complete = data.complete;
    }
    return todo;
  }))
}


const deleteTodo = async id =>{
  const data = await fetch(API_BASE + "/todo/delete/" + id, {method:"DELETE"})
  .then(res=>res.json());

  setTodos(todos => todos.filter(todo => todo._id !== data._id))
}

const addTodo = async ()=>{
  const data = await fetch (API_BASE + "/todo/new", {
    method: "post",
    headers:{
      "content-Type": "application/json"
    },
    body: JSON.stringify({
      text: newTodo
    })
  }).then(res => res.json());
  setTodos([...todos, data]);
  setPopupActive(false);
  setNewTodo("");
}


  return (
    <div className="App">
      <h1>TASKS TO DO</h1>
      <h4>your tasks</h4>
      
     { (todos.length >0)?
      <div className="todos">

{todos.map(todo => (

<div className={"todo " + (todo.complete ? "is-completed" : "")} key ={todo._id} onClick={()=> completeTodo(todo._id)}> 
<div className="checkbox"></div>
<div className="text">{ todo.text }</div>
<div className="todo"></div>
<div className="delete-todo" onClick={()=> deleteTodo(todo._id)}>x</div>
</div>

))}


       </div>
       :<h3>{`No Tasks to display add new tasks by click on "+" icon `}</h3> }
       <div className="addPopup" onClick={()=>setPopupActive(true)}>+</div>
       {popupActive ? (
<div className="popup">
  <div className="closePopup" onClick={()=>setPopupActive(false)}>x</div>
  <div className="content"><h3>Add Task</h3>
  <input type="text" id="text" className="add-todo-input"  onChange={e => setNewTodo(e.target.value) }
  value={newTodo}  />
  
  <div className="button" onClick={addTodo}>Create Task</div></div>
</div>
        ): ''}
        </div>
       
    
  );
}

export default App;
